import { NextResponse } from "next/server";
import { prisma } from "@/lib/prisma";
import { signToken } from "@/lib/auth";

export async function POST(req: Request) {
  try {
    const { email, name, sub, role } = await req.json();

    if (!email || !sub)
      return NextResponse.json({ error: "Invalid Google data" }, { status: 400 });

    let user = await prisma.user.findUnique({ where: { email } });

    if (!user) {
      user = await prisma.user.create({
        data: {
          email,
          name: name || "Google User",
          provider: "google",
          googleId: sub,
          role: role || "viewer",
          emailVerified: true,
        },
        select: { id: true, email: true, name: true, role: true },
      });
    }

    const token = signToken({ userId: user.id, email: user.email }, "7d");
    const res = NextResponse.json({ ok: true, user });
    res.headers.set("Set-Cookie", `token=${token}; Path=/; HttpOnly; Max-Age=${7 * 24 * 60 * 60}`);
    return res;
  } catch (err) {
    console.error("google signup:", err);
    return NextResponse.json({ error: "Server error" }, { status: 500 });
  }
}
